#firstSlideContainer{
    width: 100%;
    height: 700px;
    position:relative;
    color:white;
    display: flex;
    align-items: center;
}

/*background*/

#backgroundContainer{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index:-1;
}

#backgroundImage{
    background-image: url("../../assets/img/cristoRedentorEscura.png");
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
}

#backgroundImageOverlay{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    position: absolute;
    top:0;
}   

/* fim background*/

#firstSlideTextContainer{
    display: flex;
    flex-direction: column;
    padding: 0 1rem;
    gap:2rem;
    width: 100%;
    font-weight: 600;
    font-size:1.2rem;
   
}


#clientDescriptionContainer{
    display: flex;
    flex-direction: row;

}

#invisibleDiv{
    width: 55%;
    display: none;
}

@media(min-width: 768px) {

  #invisibleDiv{
    display: initial;
}
}

